import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import FormControl from '@mui/material/FormControl';
import Grid from '@mui/material/Grid';
import InputLabel from '@mui/material/InputLabel';
import Link from '@mui/material/Link';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { useInView } from 'react-intersection-observer';

import contactTel from '../images/contact-tel.jpg';
import contactMail from '../images/contact-mail.jpg';
import { PHONE, EMAIL } from '../../const';
import { Underline } from './underline';

const H2 = (props: any) => {
  const { children, sub }: { children: React.ReactNode; sub: string } = props;

  return (
    <Typography
      variant='h2'
      mr={1}
      p={0}
      lineHeight={1.5}
      sx={{
        '&::before': {
          content: `"${sub}"`,
          display: 'block',
          color: 'primary.main',
          mb: -1,
          top: 0,
          left: 0,
          fontSize: '1rem',
          letterSpacing: 2,
          lineHeight: 1
        }
      }}
    >
      {children}
    </Typography>
  );
};

const Transition = ({
  children,
  delay = 0
}: {
  children: React.ReactNode;
  delay?: number;
}) => {
  const { ref, inView } = useInView({
    //  inview 
    rootMargin: '-25% 0px',
    threshold: 0,
    triggerOnce: true
  });

  return (
    <Box
      ref={ref}
      sx={{
        width: '100%',
        height: '100%'
      }}
    >
      <Box
        sx={{
          width: '100%',
          height: '100%',
          transition: [
            `opacity 1000ms ease-out ${delay}ms`,
            `transform 1000ms ease-out ${delay}ms`
          ].join(','),
          transform: `translateX(${inView ? 0 : 100}px)`,
          opacity: inView ? 1 : 0
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

interface IContactValues {
  name: string;
  tel: string;
  email: string;
  inquiry: string;
}

export const Contact = () => {
  const [isSubmitted, setIsSubmitted] = React.useState<boolean>(false);
  const [isValidContact, setIsValidContact] = React.useState<boolean>(false);
  const [contactValues, setContactValues] = React.useState<IContactValues>({
    name: '',
    tel: '',
    email: '',
    inquiry: ''
  });
  const [isAgreePrivacy, setIsAgreePrivacy] = React.useState<0 | 1>(0);

  React.useEffect(() => {
    setIsValidContact(
      !!(
        contactValues.name &&
        contactValues.tel &&
        contactValues.email &&
        contactValues.inquiry &&
        isAgreePrivacy === 1
      )
    );
  }, [contactValues, isAgreePrivacy]);

  return (
    <Box
      id='contact'
      sx={{
        '.MuiInputBase-input': {
          color: 'text.primary'
        }
      }}
    >
      <Transition>
        <H2 sub='CONTACT'></H2>
      </Transition>
      <Typography mb={2}>
        
      </Typography>

      <Box
        maxWidth='lg'
        bgcolor='background.paper'
        mb={10}
        p={{ sm: 8, xs: 2 }}
      >
        <Box maxWidth='md' mx='auto'>
          <Box>
            {isSubmitted ? (
              <Typography>
                
                <br />
                
              </Typography>
            ) : (
              <form
                action='https://docs.google.com/forms/u/3/d/e/1FAIpQLSc9sgCiO5xXsGDPU8WgaZcSiD6VLpjj-Hh2zk5FK7EgonlDMw/formResponse'
                method='post'
                target='onSubmitIframe'
                onSubmit={e => {
                  if (!isValidContact) {
                    e.preventDefault();
                    return false;
                  }
                }}
              >
                {[
                  {
                    id: 'name',
                    type: 'text',
                    name: 'entry.417798764',
                    label: ''
                  },
                  {
                    id: 'tel',
                    type: 'tel',
                    name: 'entry.1177014290',
                    label: '',
                    required: false
                  },
                  {
                    id: 'email',
                    type: 'email',
                    name: 'entry.1396034053',
                    label: ''
                  }
                ].map(
                  ({
                    required = true,
                    id,
                    type,
                    name,
                    label
                  }: {
                    required?: boolean;
                    id: string;
                    type: string;
                    name: string;
                    label: string;
                  }) => (
                    <TextField
                      required={required}
                      key={id}
                      id={id}
                      type={type}
                      name={name}
                      label={label}
                      variant='outlined'
                      value={(contactValues as any)[id]}
                      fullWidth
                      sx={{
                        my: 2,
                        '& .MuiOutlinedInput-root': {
                          '& fieldset': {
                            borderRadius: '0px'
                          }
                        }
                      }}
                      onChange={e =>
                        setContactValues({
                          ...contactValues,
                          [id]: e.target.value
                        })
                      }
                    />
                  )
                )}
                <TextField
                  required
                  id='inquiry'
                  name='entry.2024589497'
                  label=''
                  variant='outlined'
                  multiline
                  rows={6}
                  fullWidth
                  sx={{
                    my: 2,
                    '& .MuiOutlinedInput-root': {
                      '& fieldset': {
                        borderRadius: '0px'
                      }
                    }
                  }}
                  onChange={e =>
                    setContactValues({
                      ...contactValues,
                      inquiry: e.target.value
                    })
                  }
                />
                <FormControl fullWidth>
                  <InputLabel id='demo-simple-select-label'>
                    
                  </InputLabel>
                  <Select
                    labelId='demo-simple-select-label'
                    id='demo-simple-select'
                    value={isAgreePrivacy}
                    label=''
                    onChange={e => {
                      setIsAgreePrivacy(Number(e.target.value) === 1 ? 1 : 0);
                    }}
                  >
                    <MenuItem value={0}></MenuItem>
                    <MenuItem value={1}></MenuItem>
                  </Select>
                </FormControl>

                <Box
                  display='flex'
                  flexDirection={{
                    sm: 'row',
                    xs: 'column'
                  }}
                  justifyContent='center'
                  mb={2}
                >
                  {[
                    {
                      label: '',
                      link: '/privacy/'
                    },
                    {
                      label: '',
                      link: '/recruit/privacy/'
                    }
                  ].map(({ label, link }) => (
                    <Box key={link} mx={1} component='span'>
                      <Link
                        href={link}
                        target='_blank'
                        rel='noopener'
                        underline='hover'
                      >
                        <Typography
                          color='text.primary'
                          variant='body2'
                          component='span'
                        >
                          {label}
                        </Typography>
                      </Link>
                    </Box>
                  ))}
                </Box>

                <Button
                  disabled={!isValidContact}
                  type='submit'
                  variant='contained'
                  size='large'
                  disableElevation={true}
                  sx={{
                    display: 'block',
                    mx: 'auto',
                    my: 2,
                    px: 4,
                    borderRadius: '0px'
                  }}
                >
                  
                </Button>
              </form>
            )}
            <iframe
              title='iframe'
              name='onSubmitIframe'
              style={{ display: 'none' }}
              onLoad={() => {
                setIsSubmitted(true);
              }}
            />
          </Box>
        </Box>
      </Box>
      <Grid container>
        {[
          {
            title: '',
            image: contactTel,
            note: (
              <>
                <Link href={`tel:${PHONE}`} color='text.primary'>
                  {PHONE}
                </Link>
                <br />
                08:00-17:00
              </>
            )
          },
          {
            title: '',
            image: contactMail,
            note: (
              <>
                <Link href={`mailto:${EMAIL}`} color='text.primary'>
                  {EMAIL}
                </Link>
                <br />
                24
              </>
            )
          }
        ].map(({ title, image, note }, i) => (
          <Grid item key={i} md={6} sm={12} xs={12}>
            <Box
              position='relative'
              sx={{
                width: '100%',
                height: {
                  lg: 360,
                  md: 300,
                  sm: 240,
                  xs: 180
                },
                backgroundImage: `url(${image})`,
                backgroundPosition: 'center center',
                backgroundSize: 'cover'
              }}
            >
              <Box
                position='absolute'
                display='flex'
                justifyContent='center'
                alignItems='center'
                width='100%'
                height='100%'
                bgcolor='rgba(0,0,0,0.5)'
              >
                <Box
                  sx={{
                    p: {
                      sm: 4,
                      xs: 1
                    },
                    backgroundColor: 'rgba(255,255,255,0.9)'
                  }}
                >
                  <Typography>
                    <Underline>{title}</Underline>
                  </Typography>
                  {note}
                </Box>
              </Box>
            </Box>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};
